import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';

import Dog from '../../components/dog';
import Workout from '../../components/workout';
import Team from '../../components/team';
import Track from '../../components/track';
import User from '../../components/user';
import Story from '../../components/story/publicStory';

const initialState = {};

@observer
class Search extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	loadAll = async (props = this.props) => {
		const { appState, storyStore } = this.props.stores;
		const { search } = props;
	    await appState.getSearch(search);
		const { publicTeams, publicUsers } = appState;
		storyStore.localUpdateField('publicTeams', publicTeams);
		storyStore.localUpdateField('publicUsers', publicUsers);
	}

	componentDidMount() {
		this.loadAll();
	}

	componentWillReceiveProps(nextProps) {
        // console.log('componentWillReceiveProps', nextProps);
        if (nextProps.search !== this.props.search) {
            this.loadAll(nextProps);
        }
    }

	render() {
		const { appState } = this.props.stores;
		const { searchResultStories, searchResultDogs, searchResultWorkouts, searchResultTeams, searchResultTracks, searchResultUsers } = appState;
		return (
			<div class='container-fluid' style='margin-bottom: 200px; margin-top: 60px;'>
				<div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-0 mb-2'>
						<h1><Text id='search.title'>SÃ¸k</Text></h1>
						
						<h5>searchResultDogs:</h5>
						{/* <xmp>{JSON.stringify(searchResultDogs, null, 4)}</xmp> */}
						{searchResultDogs && searchResultDogs.map(e => <>
							<div>{e.id}: {e.user}: {e.name}</div>
						</>)}
					</div>
					<div class='col-12'>
						{searchResultDogs && searchResultDogs.map(dog => (
							<Dog stores={this.props.stores} dog={dog} />
						))}
					</div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-0 mb-2'>
						<h5>searchResultWorkouts:</h5>
						{/* <xmp>{JSON.stringify(searchResultWorkouts, null, 4)}</xmp> */}
						{searchResultWorkouts && searchResultWorkouts.map(e => <>
							<div>{e.id}: {e.user}: {e.name}</div>
						</>)}
					</div>
					<div class='col-12'>
						{searchResultWorkouts && searchResultWorkouts.map(workout => (
							<Workout stores={this.props.stores} workout={workout} showDate={true} />
						))}
					</div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-0 mb-2'>
						<h5>searchResultTeams:</h5>
						{/* <xmp>{JSON.stringify(searchResultTeams, null, 4)}</xmp> */}
						{searchResultTeams && searchResultTeams.map(e => <>
							<div>{e.id}: {e.user}: {e.name}</div>
						</>)}
					</div>
					<div class='col-12'>
						{searchResultTeams && searchResultTeams.map(team => (
							<Team stores={this.props.stores} team={team} />
						))}
					</div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-0 mb-2'>
						<h5>searchResultTracks:</h5>
						{/* <xmp>{JSON.stringify(searchResultTracks, null, 4)}</xmp> */}
						{searchResultTracks && searchResultTracks.map(e => <>
							<div>{e.id}: {e.user}: {e.name}</div>
						</>)}
					</div>
					<div class='col-12'>
						{searchResultTracks && searchResultTracks.map(track => (
							<Track stores={this.props.stores} track={track} />
						))}
					</div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-0 mb-2'>
						<h5>searchResultUsers:</h5>
						{/* <xmp>{JSON.stringify(searchResultUsers, null, 4)}</xmp> */}
						{searchResultUsers && searchResultUsers.map(e => <>
							<div>{e.id}: {e.user}: {e.email}</div>
						</>)}
					</div>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-0 mb-2'>
						{searchResultUsers && searchResultUsers.map(user => (
							<User stores={this.props.stores} user={user} />
						))}
					</div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-0 mb-2'>
						<h5>searchResultStories:</h5>
						{/* <xmp>{JSON.stringify(searchResultStories, null, 4)}</xmp> */}
						{searchResultStories && searchResultStories.map(e => <>
							<div>{e.id}: {e.user}: {e.title}</div>
						</>)}
					</div>
					<div class='col-12'>
						{searchResultStories && searchResultStories.map(story => (
							<Story
								stores={this.props.stores}
								story={story}
								key={`story-detail-${story.id}`}
							/>
						))}
					</div>

				</div>
			</div>
		);
	}
}

export default Search;
